'use client';
import React from 'react';
import { Box, Button, Step, StepLabel, Stepper, TextField, Typography, Card, CardContent, Checkbox, FormControlLabel, MenuItem, Stack } from '@mui/material';
import { useSession } from '@/features/auth/session';
import { listClientsByAgencyEmail } from '@/services/clients';
import { getDivisions, getStates, getUniversities, getUniversity } from '@/services/recruiterMeta';

type ClientRow = { id: string; email: string; firstName?: string; lastName?: string; sport?: string };

export function RecruiterWizard() {
  const { session } = useSession();
  const [activeStep, setActiveStep] = React.useState(0);

  // Step 1 - client selection
  const [clients, setClients] = React.useState<ClientRow[]>([]);
  const [clientId, setClientId] = React.useState<string>('');

  // Step 2 - division/state/schools
  const [divisions, setDivisions] = React.useState<string[]>([]);
  const [division, setDivision] = React.useState<string>('');
  const [states, setStates] = React.useState<Array<{ code: string; name: string }>>([]);
  const [state, setState] = React.useState<string>('');
  const [schools, setSchools] = React.useState<Array<{ id: string; name: string }>>([]);

  // Step 3 - school details and coach selection
  const [selectedSchoolId, setSelectedSchoolId] = React.useState<string>('');
  const [schoolDetails, setSchoolDetails] = React.useState<any>(null);
  const [selectedCoachIds, setSelectedCoachIds] = React.useState<Record<string, boolean>>({});

  // Draft
  const [draft, setDraft] = React.useState<string>('');

  React.useEffect(() => {
    if (!session) return;
    if (session.role !== 'agency') return;
    listClientsByAgencyEmail(session.email).then(setClients);
    getDivisions().then(setDivisions);
  }, [session]);

  React.useEffect(() => {
    if (!division) {
      setStates([]);
      setState('');
      setSchools([]);
      return;
    }
    getStates(division).then(setStates);
    setState('');
    setSchools([]);
  }, [division]);

  React.useEffect(() => {
    if (division && state) {
      getUniversities({ division, state }).then(setSchools);
    } else {
      setSchools([]);
    }
  }, [division, state]);

  React.useEffect(() => {
    if (!selectedSchoolId) {
      setSchoolDetails(null);
      setSelectedCoachIds({});
      return;
    }
    getUniversity(selectedSchoolId).then((u) => {
      setSchoolDetails(u);
      setSelectedCoachIds({});
    });
  }, [selectedSchoolId]);

  const isLast = activeStep === 3;
  const canNext =
    (activeStep === 0 && Boolean(clientId)) ||
    (activeStep === 1 && Boolean(division) && Boolean(state) && schools.length > 0) ||
    (activeStep === 2 && Boolean(selectedSchoolId)) ||
    (activeStep === 3);

  const handleNext = () => {
    if (isLast) {
      // generate a simple draft
      const chosen = Object.entries(selectedCoachIds)
        .filter(([, checked]) => checked)
        .map(([id]) => schoolDetails?.coaches?.find((c: any) => c.id === id))
        .filter(Boolean);
      const coachLines =
        chosen.length > 0
          ? chosen.map((c: any) => `- ${c.firstName} ${c.lastName}${c.email ? ` (${c.email})` : ''}`).join('\n')
          : 'No coaches selected';
      const body = [
        `School: ${schoolDetails?.name}`,
        `Division: ${schoolDetails?.division}`,
        `Location: ${schoolDetails?.city}, ${schoolDetails?.state}`,
        `Coaches:\n${coachLines}`,
      ].join('\n');
      setDraft(body);
      return;
    }
    setActiveStep((s) => s + 1);
  };
  const handleBack = () => setActiveStep((s) => Math.max(0, s - 1));

  const toggleCoach = (id: string) => {
    setSelectedCoachIds((prev) => ({ ...prev, [id]: !prev[id] }));
  };

  return (
    <Box>
      <Stepper activeStep={activeStep} alternativeLabel sx={{ mb: 3 }}>
        {['Select Client', 'Universities', 'Details & Coaches', 'Draft'].map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <Box sx={{ mb: 2 }}>
        {activeStep === 0 && (
          <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 2, maxWidth: 700 }}>
            <TextField
              select
              label="Client"
              value={clientId}
              onChange={(e) => setClientId(e.target.value)}
              SelectProps={{ MenuProps: { disablePortal: true } }}
            >
              {clients.map((c) => (
                <MenuItem key={c.id} value={c.id}>
                  {c.email} {c.firstName ? `- ${c.firstName} ${c.lastName}` : ''}
                </MenuItem>
              ))}
            </TextField>
          </Box>
        )}
        {activeStep === 1 && (
          <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 2, maxWidth: 800 }}>
            <TextField
              select
              label="Division"
              value={division}
              onChange={(e) => setDivision(e.target.value)}
              SelectProps={{ MenuProps: { disablePortal: true } }}
            >
              {divisions.map((d) => (
                <MenuItem key={d} value={d}>
                  {d}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              select
              label="State"
              value={state}
              onChange={(e) => setState(e.target.value)}
              disabled={!division}
              SelectProps={{ MenuProps: { disablePortal: true } }}
            >
              {states.map((s) => (
                <MenuItem key={s.code} value={s.code}>
                  {s.name}
                </MenuItem>
              ))}
            </TextField>
            <Box sx={{ gridColumn: '1 / -1' }}>
              <Typography variant="subtitle2" color="text.secondary" sx={{ mb: 1 }}>
                Universities
              </Typography>
              <Stack direction="row" spacing={2} sx={{ flexWrap: 'wrap' }}>
                {schools.map((u) => (
                  <Card
                    key={u.id}
                    onClick={() => setSelectedSchoolId(u.id)}
                    sx={{
                      width: 260,
                      cursor: 'pointer',
                      outline: selectedSchoolId === u.id ? '2px solid #1976d2' : 'none',
                    }}
                  >
                    <CardContent>
                      <Typography>{u.name}</Typography>
                    </CardContent>
                  </Card>
                ))}
              </Stack>
            </Box>
          </Box>
        )}
        {activeStep === 2 && schoolDetails && (
          <Box sx={{ maxWidth: 900 }}>
            <Typography variant="h6" gutterBottom>
              {schoolDetails.name}
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
              {schoolDetails.city}, {schoolDetails.state} — {schoolDetails.division} {schoolDetails.conference ? `• ${schoolDetails.conference}` : ''}
            </Typography>
            <Typography variant="subtitle1" sx={{ mb: 1 }}>
              Coaches
            </Typography>
            <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 1 }}>
              {schoolDetails.coaches?.map((c: any) => (
                <FormControlLabel
                  key={c.id}
                  control={<Checkbox checked={Boolean(selectedCoachIds[c.id])} onChange={() => toggleCoach(c.id)} />}
                  label={`${c.firstName} ${c.lastName} — ${c.title}${c.email ? ` (${c.email})` : ''}`}
                />
              ))}
            </Box>
          </Box>
        )}
        {activeStep === 3 && draft && (
          <Box>
            <Typography variant="h6" gutterBottom>
              Draft
            </Typography>
            <pre style={{ whiteSpace: 'pre-wrap' }}>{draft}</pre>
          </Box>
        )}
      </Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
        <Button disabled={activeStep === 0} onClick={handleBack}>
          Back
        </Button>
        <Button variant="contained" onClick={handleNext} disabled={!canNext}>
          {isLast ? 'Generate' : 'Next'}
        </Button>
      </Box>
    </Box>
  );
}


